@font-face {
  font-family: 'martin-web';
  src: url('martin-web/MARTIN-Regular.eot'); /* IE9 Compat Modes */
  src: url('martin-web/MARTIN-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('martin-web/MARTIN-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('martin-web/MARTIN-Regular.woff') format('woff');
}

html, body {
  padding:0px;
  margin:0px;
  
}

* {
  box-sizing: border-box;
}

body { 
  background-color:black;
  color:white;
  font-family:monospace;
  color:#949393;
}

a {
  color:white;
  color: #5f5f5f;
}

h1,h2 { 
  text-align:center;
  font-weight:500;

}

h1 {
  font-family:"martin-web", sans-serif;
  font-size:190px;
  margin-bottom:0;
  line-height: .7em;
  text-transform:uppercase;
  color:#3c3b3b;
  padding: 50px;
}

h2 {
  margin-bottom:150px;
  margin-top:30px;
  font-weight:normal;
  font-size:22px;
  padding: 50px;
  line-height: 1.4em;
}

.namelist {
  max-width:1040px;
  margin:0 auto;


  &__entry {
    position:relative;
    min-height:20px;
  }

  &__line {
    position:absolute;
    left:40%;
    top:0px;
    height:100%;
    width:3px;
    background-color:#272727;
    display:none;
  }

  &__dot { 
    position:absolute;
    background-color:#272727;
    border-radius:40%;
    width:11px;
    height:11px;
    top:3px;
    left:-5px;
    border:2px solid black;
  }

  &__date {
    font-size:20px;
  }


  &__details {
    position:absolute;
    right: 60%;
    padding-right:10px;
    font-size:14px;
    padding-bottom:50px;
    top:15px;
    max-width:40%;
    text-align:right;
  }

  &__name { 
    position:relative;
    left: 40%;
    padding-left:16px;
    font-family:"martin-web";
    text-transform:uppercase;
    font-size:120px;
    color:white;
    line-height:.7em;
    letter-spacing:.015em;
    word-spacing:.015em;
    padding-bottom:50px;
  }
  
  &__age {
    margin-top:5px;
  }

  &__image {
    width:400px;
    height:140px;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: left top;

  }

}

@media screen and (max-width:600px) {
  
  .namelist {

  &__entry {
  }

  &__line {
    left:30%;
  }

  &__date {
    right: 70%;
  }
    
  &__details { 
    left: 30%;
  }



}

  
  
  
}